@import url(https://fonts.googleapis.com/css?family=Noto+Sans+SC&display=swap);
.nav-logo {
  width: 210px;
}

@media (min-width: 768px) {
  .nav-logo {
    width: 270px;
  }
}
.home-overlay {
  height: 100vh;
  background-image: url("/home-background.jpg");
  background-size: cover;
  background-position: center;
}

.overlay-mask {
  height: 100%;
  width: 100%;
  background: rgba(0, 0, 0, 0.65);
  display: flex;
  /* justify-content: center; */
  flex-direction: column;
}

.home-text {
  font-weight: bold;
  color: #fff;
  font-size: 9vw;
  letter-spacing: 3px;
}

.home-text-eng {
  font-weight: bold;
  color: #fff;
  text-transform: uppercase;
  font-size: 2.5vw;
}

.section-wrapper {
  background: #fafafa;
}

.description-logo {
  max-width: 100%;
  max-height: 100%;
}

.description-text {
  font-size: 14px;
}

.description-gap {
  height: 20px;
}

.description {
  padding-top: 70px;
  padding-bottom: 70px;
}

@media (min-width: 576px) {
  .home-text {
    font-size: 7vw;
  }
  
  .home-text-eng {
    font-size: 2vw;
  }
}

@media (min-width: 768px) {
  .home-text {
    font-size: 5vw;
  }
  
  .home-text-eng {
    font-size: 1.8vw;
  }
}

@media (min-width: 992px) {
  .description-text {
    font-size: 18px;
  }
}

@media (min-width: 1200px) {
  .home-text {
    font-size: 4vw;
  }
  
  .home-text-eng {
    font-size: 1.4vw;
  }
}


.section-wrapper {
  background: #fafafa;
}

.copyright {
  font-size: 7px;
  margin-bottom: 7px;
}

.footer-logos {
  max-width: 100%;
}

.footer-text {
  font-size: 30px;
}

.underline {
  background: #212529;
  width: 80px;
  height: 2px;
  margin: auto;
  margin-top: 5px;
  margin-bottom: 30px;
}

.footer-qrcode {
  max-width: 40%;
  margin-bottom: 50px;
}

.follow-link:hover {
  transform: scale(1.08);
  -webkit-transform: scale(1.08);
  -moz-transform: scale(1.08);
  -ms-transform: scale(1.08);
  -o-transform: scale(1.08);
}

@media (min-width: 576px) {
  .copyright {
    font-size: 10px;
    margin-bottom: 10px;
  }

  .footer-qrcode {
    max-width: 35%
  }
}

@media (min-width: 768px) {
  .footer-logos {
    max-width: 60%;
  }

  .footer-text {
    font-size: 40px;
  }

  .footer-qrcode {
    max-width: 200px;
  }
}

@media (min-width: 992px) {
  .footer-logos {
    max-width: 50%;
  }
}

@media (min-width: 1200px) {
  .copyright {
    font-size: 13px;
    margin-bottom: 13px;
  }

  .footer-logos {
    max-width: 40%;
  }
}
.card-pic {
  width: 100%;
}

.card-img {
  overflow: hidden;
}

.card {
  margin-top: 60px;
  -webkit-transition: 0.3s;
  transition: 0.3s;
}

.card:hover {
  cursor: pointer;
  /* transform: scale(1.01);
  -webkit-transform: scale(1.01);
  -moz-transform: scale(1.01);
  -ms-transform: scale(1.01);
  -o-transform: scale(1.01); */
}

.card-text {
  font-size: 14px;
  color: #545454;
}

